.pets {
	padding: 90px 0 45px 0;
	background-color: #ffffff;
	@media(max-width: 1024px) {
		padding: 70px 0 35px 0;
	}
	@media(max-width: 768px) {
		padding: 60px 0 30px 0;
	}
	@media(max-width: 480px) {
		padding: 40px 0 20px 0;
	}

	&__title {
		font-family: "Comfortaa";
		font-weight: 400;
		font-size: 50px;
		line-height: 60px;
		margin-bottom: 50px;
		@media(max-width: 1024px) {
			font-size: 44px;
			line-height: 50px;
			margin-bottom: 40px;
		}
		@media(max-width: 768px) {
			font-size: 30px;
			line-height: 34px;
			margin-bottom: 32px;
		}
		@media(max-width: 480px) {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 20px;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(12, auto);
		grid-template-rows: auto auto;
		grid-template-areas: 
		"one one one one one two two two three three three three"
        "four four four five five five six six three three three three";
		row-gap: 44px;
		column-gap: 20px;
		@media(max-width: 1024px) {
			grid-template-columns: repeat(9, auto);
			grid-template-areas: 
				"one one one one two two three three three"
				"four four five five six six three three three";
				row-gap: 62px;
		}
		@media(max-width: 768px) {
			display: unset;
		}
	}

	&__item {
		@media(max-width: 768px) {
			max-width: 700px;
		}
		
		&_one {
			grid-area: one;
		}

		&_two {
			grid-area: two;
		}

		&_three {
			grid-area: three;
		}

		&_four {
			grid-area: four;
		}

		&_five {
			grid-area: five;
		}

		&_six {
			grid-area: six;
		}
	}

	&__img {
	
		width: 100%;
		object-fit: cover;
		
		@media(max-width: 768px) {
			object-fit: cover;
		}
		@media(max-width: 480px) {
			object-fit: contain;
		}
		&_one { 
			height: 280px;
			@media(max-width: 1024px) {
				height: 201px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 540px;
				object-position: bottom 0 right -27px;
			}
			@media(max-width: 480px) {
				height: 184px;
				object-position: left;
			} 
		}
		&_two { 
			height: 280px;
			@media(max-width: 1024px) {
				height: 201px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 459px;
			}
			@media(max-width: 480px) {
				height: 184px;
				object-position: left;
			} 
		}
		&_three { 
			height: 650px;
			object-position: left;
			@media(max-width: 1024px) {
				height: 478px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 280px;
			}
			@media(max-width: 480px) {
				height: 184px;
				object-position: center;
			} 
		}
		&_four {
			height: 280px;
			@media(max-width: 1024px) {
				height: 180px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 394px;
			}
			@media(max-width: 480px) {
				height: 184px;
				object-position: left;
			} 
		}
		&_five {
			height: 280px;
			@media(max-width: 1024px) {
				height: 180px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 295px;
			}
			@media(max-width: 480px) {
				height: 184px;
				object-position: left;
			} 
		}
		&_six {
			height: 280px;
			@media(max-width: 1024px) {
				height: 280px;
			}
			@media(max-width: 768px) {
				height: 368px;
				width: 237px;
			}
			@media(max-width: 480px) {
				height: 184px;
			} 
		}
	}

	&__figure {
		
		&_one {
			max-width: 480px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 540px;
			}

		}

		&_two {
			max-width: 280px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 459px;
			}
		}

		&_three {
			max-width: 380px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 280px;
			}
		}

		&_four {
			max-width: 280px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 394px;
			}
		}

		&_five {
			max-width: 280px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 295px;
			}
		}

		&_six {
			max-width: 180px;
			width: 100%;
			@media(max-width: 1024px) {
				max-width: 100%;
			}
			@media(max-width: 768px) {
				max-width: 237px;
			}
		}
	}

	&__figcaption {
		padding-top: 12px;
		font-size: 20px;
		line-height: 26px;
		@media(max-width: 1024px) {
			font-size: 18px;
			line-height: 24px;
			padding-top: 0;
		}
		@media(max-width: 480px) {
			font-size: 14px;
			line-height: 20px;
		}
	}
}